'use client';

import React, { useState } from 'react';
import { Plus, Edit, Trash2, Menu, X, LogOut, BarChart3, Users, BookOpen, Settings } from 'lucide-react';

interface Course {
  id: number;
  class_date: string;
  session: string;
  start_time: string;
  end_time: string;
  menu_id: number;
  capacity: number;
  price: number;
  is_special: boolean;
  enrolled?: number;
}

interface FormData extends Omit<Course, 'id'> {}

interface MenuItem {
  label: string;
  value: 'courses' | 'courselist' | 'students' | 'settings';
  icon: React.ReactNode;
}

export default function AdminDashboard() {
  const [sidebarOpen, setSidebarOpen] = useState<boolean>(true);
  const [currentPage, setCurrentPage] = useState<'courses' | 'courselist' | 'students' | 'settings'>('courses');
  const [courses, setCourses] = useState<Course[]>([
    {
      id: 1,
      class_date: '2025-11-12',
      session: 'Thai Herbs & Spices Cooking Class',
      start_time: '16:00',
      end_time: '18:00',
      menu_id: 101,
      capacity: 12,
      price: 1500,
      is_special: false,
      enrolled: 8
    },
    {
      id: 2,
      class_date: '2025-11-13',
      session: 'Massaman Thai Cooking',
      start_time: '16:00',
      end_time: '18:00',
      menu_id: 102,
      capacity: 10,
      price: 1200,
      is_special: false,
      enrolled: 10
    },
    {
      id: 3,
      class_date: '2025-11-12',
      session: 'Coconut & Curry Cooking Experience',
      start_time: '16:00',
      end_time: '18:00',
      menu_id: 103,
      capacity: 15,
      price: 1800,
      is_special: true,
      enrolled: 12
    },
    {
      id: 4,
      class_date: '2025-11-12',
      session: 'Thai Curry Masterclass',
      start_time: '16:00',
      end_time: '18:00',
      menu_id: 104,
      capacity: 10,
      price: 1600,
      is_special: false,
      enrolled: 7
    },
    {
      id: 5,
      class_date: '2025-11-13',
      session: 'Thai Street Food Favorites',
      start_time: '16:00',
      end_time: '18:00',
      menu_id: 105,
      capacity: 12,
      price: 1400,
      is_special: false,
      enrolled: 9
    },
    {
      id: 6,
      class_date: '2025-11-13',
      session: 'Sweet Thai Classics Workshop',
      start_time: '15:00',
      end_time: '17:00',
      menu_id: 106,
      capacity: 8,
      price: 1300,
      is_special: true,
      enrolled: 6
    }
  ]);

  const [showForm, setShowForm] = useState<boolean>(false);
  const [editingId, setEditingId] = useState<number | null>(null);
  const [formData, setFormData] = useState<FormData>({
    class_date: '',
    session: '',
    start_time: '',
    end_time: '',
    menu_id: 0,
    capacity: 0,
    price: 0,
    is_special: false,
    enrolled: 0
  });

  const handleAddCourse = (): void => {
    setEditingId(null);
    setFormData({ 
      class_date: '', 
      session: '', 
      start_time: '', 
      end_time: '', 
      menu_id: 0, 
      capacity: 0, 
      price: 0, 
      is_special: false, 
      enrolled: 0 
    });
    setShowForm(true);
  };

  const handleEditCourse = (course: Course): void => {
    setEditingId(course.id);
    setFormData({
      class_date: course.class_date,
      session: course.session,
      start_time: course.start_time,
      end_time: course.end_time,
      menu_id: course.menu_id,
      capacity: course.capacity,
      price: course.price,
      is_special: course.is_special,
      enrolled: course.enrolled || 0
    });
    setShowForm(true);
  };

  const handleSaveCourse = (): void => {
    if (editingId) {
      setCourses(courses.map(c => c.id === editingId ? { ...c, ...formData } : c));
    } else {
      setCourses([...courses, { id: Date.now(), ...formData }]);
    }
    setShowForm(false);
    setFormData({ 
      class_date: '', 
      session: '', 
      start_time: '', 
      end_time: '', 
      menu_id: 0, 
      capacity: 0, 
      price: 0, 
      is_special: false, 
      enrolled: 0 
    });
  };

  const handleDeleteCourse = (id: number): void => {
    setCourses(courses.filter(c => c.id !== id));
  };

  const menuItems: MenuItem[] = [
    { label: 'Dashboard', value: 'courses', icon: <BarChart3 size={20} /> },
    { label: 'Courses', value: 'courselist', icon: <BookOpen size={20} /> },
    { label: 'Students', value: 'students', icon: <Users size={20} /> },
    { label: 'Settings', value: 'settings', icon: <Settings size={20} /> }
  ];

  const totalEnrollments: number = courses.reduce((sum, c) => sum + (c.enrolled || 0), 0);
  const totalCapacity: number = courses.reduce((sum, c) => sum + c.capacity, 0);
  const totalRevenue: number = courses.reduce((sum, c) => sum + c.price * (c.enrolled || 0), 0);

  const formatTime = (time: string): string => {
    if (!time) return '';
    const [hours, minutes] = time.split(':');
    const hour = parseInt(hours);
    const ampm = hour >= 12 ? 'PM' : 'AM';
    const displayHour = hour % 12 || 12;
    return `${displayHour}:${minutes} ${ampm}`;
  };

  const formatDate = (dateStr: string): string => {
    if (!dateStr) return '';
    const date = new Date(dateStr + 'T00:00:00');
    return date.toLocaleDateString('en-US', { month: 'short', day: 'numeric', year: 'numeric' }).toUpperCase();
  };

  return (
    <div className="flex h-screen" style={{ backgroundColor: '#f5f1ed' }}>
      {/* Sidebar */}
      <div className={`${sidebarOpen ? 'w-64' : 'w-20'} transition-all duration-300 flex flex-col shadow-lg`} style={{ backgroundColor: '#3d2817' }}>
        <div className="p-6 border-b flex items-center justify-between" style={{ borderColor: '#5c3d2e' }}>
          {sidebarOpen && <h1 className="text-2xl font-light" style={{ color: '#d4a574' }}>Nal Baan</h1>}
          <button onClick={() => setSidebarOpen(!sidebarOpen)} className="text-white hover:opacity-80">
            {sidebarOpen ? <X size={20} /> : <Menu size={20} />}
          </button>
        </div>

        <nav className="flex-1 p-4 space-y-2">
          {menuItems.map((item) => (
            <button
              key={item.value}
              onClick={() => setCurrentPage(item.value)}
              className={`w-full text-left p-4 rounded-lg transition-all ${
                currentPage === item.value
                  ? 'text-white'
                  : 'text-amber-100 hover:text-white'
              }`}
              style={{
                backgroundColor: currentPage === item.value ? '#5c3d2e' : 'transparent'
              }}
            >
              <div className="flex items-center gap-3">
                {item.icon}
                {sidebarOpen && <span>{item.label}</span>}
              </div>
            </button>
          ))}
        </nav>

        {sidebarOpen && (
          <div className="p-4" style={{ borderTop: '1px solid #5c3d2e' }}>
            <button className="w-full flex items-center gap-2 text-amber-100 hover:text-white text-sm transition">
              <LogOut size={18} /> Logout
            </button>
          </div>
        )}
      </div>

      {/* Main Content */}
      <div className="flex-1 flex flex-col">
        {/* Header */}
        <div className="bg-white border-b px-8 py-6 shadow-sm" style={{ borderColor: '#e5dcd4' }}>
          <h2 className="text-4xl font-light" style={{ color: '#3d2817' }}>
            {currentPage === 'courses' && 'Dashboard'}
            {currentPage === 'courselist' && 'Manage Courses'}
            {currentPage === 'students' && 'Students'}
            {currentPage === 'settings' && 'Settings'}
          </h2>
          <p className="text-sm mt-1" style={{ color: '#8b6f47' }}>Welcome back to Nal Baan Cooking Studio</p>
        </div>

        {/* Content */}
        <div className="flex-1 overflow-auto p-8">
          {currentPage === 'courses' && (
            <div className="space-y-8">
              {/* Stats */}
              <div className="grid grid-cols-4 gap-6">
                <div className="bg-white p-8 rounded-lg shadow-sm border" style={{ borderColor: '#e5dcd4', backgroundColor: '#fffbf7' }}>
                  <div className="flex items-center justify-between">
                    <div>
                      <p className="text-sm font-light" style={{ color: '#8b6f47' }}>Total Courses</p>
                      <p className="text-4xl font-light mt-2" style={{ color: '#3d2817' }}>{courses.length}</p>
                    </div>
                    <div className="text-5xl opacity-20">üìö</div>
                  </div>
                </div>

                <div className="bg-white p-8 rounded-lg shadow-sm border" style={{ borderColor: '#e5dcd4', backgroundColor: '#fffbf7' }}>
                  <div className="flex items-center justify-between">
                    <div>
                      <p className="text-sm font-light" style={{ color: '#8b6f47' }}>Enrollments</p>
                      <p className="text-4xl font-light mt-2" style={{ color: '#3d2817' }}>{totalEnrollments}</p>
                    </div>
                    <div className="text-5xl opacity-20">üë•</div>
                  </div>
                </div>

                <div className="bg-white p-8 rounded-lg shadow-sm border" style={{ borderColor: '#e5dcd4', backgroundColor: '#fffbf7' }}>
                  <div className="flex items-center justify-between">
                    <div>
                      <p className="text-sm font-light" style={{ color: '#8b6f47' }}>Capacity</p>
                      <p className="text-4xl font-light mt-2" style={{ color: '#3d2817' }}>{totalCapacity}</p>
                    </div>
                    <div className="text-5xl opacity-20">ü™ë</div>
                  </div>
                </div>

                <div className="bg-white p-8 rounded-lg shadow-sm border" style={{ borderColor: '#e5dcd4', backgroundColor: '#fffbf7' }}>
                  <div className="flex items-center justify-between">
                    <div>
                      <p className="text-sm font-light" style={{ color: '#8b6f47' }}>Revenue</p>
                      <p className="text-4xl font-light mt-2" style={{ color: '#3d2817' }}>‡∏ø{(totalRevenue / 1000).toFixed(1)}K</p>
                    </div>
                    <div className="text-5xl opacity-20">üí∞</div>
                  </div>
                </div>
              </div>

              {/* Course Grid */}
              <div>
                <div className="flex justify-between items-center mb-6">
                  <h3 className="text-2xl font-light" style={{ color: '#3d2817' }}>Course Highlights</h3>
                  <button
                    onClick={handleAddCourse}
                    className="px-6 py-2 rounded-lg text-white flex items-center gap-2 font-light hover:shadow-lg transition"
                    style={{ backgroundColor: '#3d2817' }}
                  >
                    <Plus size={18} /> Add Course
                  </button>
                </div>

                <div className="grid grid-cols-3 gap-6">
                  {courses.map(course => (
                    <div key={course.id} className="bg-white rounded-lg overflow-hidden shadow-md hover:shadow-lg transition border" style={{ borderColor: '#e5dcd4' }}>
                      <div className="h-48 bg-gradient-to-br opacity-30" style={{ background: 'linear-gradient(135deg, #8b6f47 0%, #5c3d2e 100%)' }}></div>
                      
                      <div className="p-5">
                        <div className="flex justify-between items-start mb-2">
                          <h4 className="font-light text-sm flex-1" style={{ color: '#3d2817' }}>{course.session}</h4>
                          {course.is_special && <span className="text-xs px-2 py-1 rounded-full" style={{ backgroundColor: '#d4a574', color: 'white' }}>Special</span>}
                        </div>
                        
                        <div className="space-y-2 mb-4 text-xs" style={{ color: '#8b6f47' }}>
                          <p>üìÖ {formatDate(course.class_date)}</p>
                          <p>‚è∞ {formatTime(course.start_time)} - {formatTime(course.end_time)}</p>
                          <p>üîñ Menu ID: {course.menu_id}</p>
                        </div>

                        <div className="mb-4 pb-4 border-t" style={{ borderColor: '#e5dcd4' }}>
                          <div className="mt-4 flex justify-between text-xs">
                            <span style={{ color: '#8b6f47' }}>Enrolled: {course.enrolled || 0}/{course.capacity}</span>
                            <span style={{ color: '#3d2817' }} className="font-semibold">‡∏ø{course.price}</span>
                          </div>
                          <div className="mt-2 bg-gray-200 rounded-full h-1.5" style={{ backgroundColor: '#e5dcd4' }}>
                            <div
                              className="h-full rounded-full transition-all"
                              style={{
                                width: `${((course.enrolled || 0) / course.capacity) * 100}%`,
                                backgroundColor: '#d4a574'
                              }}
                            ></div>
                          </div>
                        </div>

                        <div className="flex gap-2 justify-end">
                          <button
                            onClick={() => handleEditCourse(course)}
                            className="p-2 rounded hover:shadow transition"
                            style={{ backgroundColor: '#f5f1ed' }}
                          >
                            <Edit size={16} style={{ color: '#3d2817' }} />
                          </button>
                          <button
                            onClick={() => handleDeleteCourse(course.id)}
                            className="p-2 rounded hover:shadow transition"
                            style={{ backgroundColor: '#f5f1ed' }}
                          >
                            <Trash2 size={16} style={{ color: '#c1513b' }} />
                          </button>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          )}

          {currentPage === 'courselist' && (
            <div className="bg-white rounded-lg shadow-md border overflow-hidden" style={{ borderColor: '#e5dcd4' }}>
              <div className="px-8 py-6 border-b flex justify-between items-center" style={{ borderColor: '#e5dcd4' }}>
                <h3 className="text-xl font-light" style={{ color: '#3d2817' }}>All Courses</h3>
                <button
                  onClick={handleAddCourse}
                  className="px-4 py-2 rounded text-white flex items-center gap-2 font-light text-sm hover:shadow transition"
                  style={{ backgroundColor: '#3d2817' }}
                >
                  <Plus size={16} /> Add Course
                </button>
              </div>

              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead>
                    <tr style={{ backgroundColor: '#f5f1ed', borderBottom: '1px solid #e5dcd4' }}>
                      <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Session</th>
                      <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Date</th>
                      <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Time</th>
                      <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Capacity</th>
                      <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Enrolled</th>
                      <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Price</th>
                      <th className="px-6 py-4 text-left text-xs font-light uppercase" style={{ color: '#8b6f47' }}>Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {courses.map(course => (
                      <tr key={course.id} className="border-b hover:opacity-75 transition" style={{ borderColor: '#e5dcd4' }}>
                        <td className="px-6 py-4 text-sm" style={{ color: '#3d2817' }}>
                          <div className="flex items-center gap-2">
                            {course.session}
                            {course.is_special && <span className="text-xs px-2 py-0.5 rounded-full" style={{ backgroundColor: '#d4a574', color: 'white' }}>Special</span>}
                          </div>
                        </td>
                        <td className="px-6 py-4 text-sm" style={{ color: '#8b6f47' }}>{formatDate(course.class_date)}</td>
                        <td className="px-6 py-4 text-sm" style={{ color: '#8b6f47' }}>{formatTime(course.start_time)} - {formatTime(course.end_time)}</td>
                        <td className="px-6 py-4 text-sm" style={{ color: '#8b6f47' }}>{course.capacity}</td>
                        <td className="px-6 py-4 text-sm">
                          <span className="px-3 py-1 rounded-full text-xs font-light" style={{
                            backgroundColor: (course.enrolled || 0) >= course.capacity ? '#f5d5d5' : '#e8f5e9',
                            color: (course.enrolled || 0) >= course.capacity ? '#c1513b' : '#558b2f'
                          }}>
                            {course.enrolled || 0}/{course.capacity}
                          </span>
                        </td>
                        <td className="px-6 py-4 text-sm font-light" style={{ color: '#3d2817' }}>‡∏ø{course.price}</td>
                        <td className="px-6 py-4 text-sm space-x-2 flex">
                          <button
                            onClick={() => handleEditCourse(course)}
                            className="p-2 rounded transition"
                            style={{ color: '#3d2817', backgroundColor: '#f5f1ed' }}
                          >
                            <Edit size={16} />
                          </button>
                          <button
                            onClick={() => handleDeleteCourse(course.id)}
                            className="p-2 rounded transition"
                            style={{ color: '#c1513b', backgroundColor: '#f5f1ed' }}
                          >
                            <Trash2 size={16} />
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          )}

          {currentPage === 'students' && (
            <div className="bg-white p-12 rounded-lg shadow-md text-center border" style={{ borderColor: '#e5dcd4' }}>
              <Users size={48} className="mx-auto mb-4" style={{ color: '#d4a574', opacity: 0.3 }} />
              <p style={{ color: '#8b6f47' }}>Students management coming soon</p>
            </div>
          )}

          {currentPage === 'settings' && (
            <div className="bg-white p-12 rounded-lg shadow-md text-center border" style={{ borderColor: '#e5dcd4' }}>
              <Settings size={48} className="mx-auto mb-4" style={{ color: '#d4a574', opacity: 0.3 }} />
              <p style={{ color: '#8b6f47' }}>Settings coming soon</p>
            </div>
          )}
        </div>
      </div>

      {/* Add/Edit Modal */}
      {showForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-8 max-w-md w-full mx-4 shadow-lg" style={{ backgroundColor: '#fffbf7' }}>
            <h3 className="text-2xl font-light mb-6" style={{ color: '#3d2817' }}>
              {editingId ? 'Edit Course' : 'Add New Course'}
            </h3>

            <div className="space-y-4">
              <div>
                <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Session Name</label>
                <input
                  type="text"
                  value={formData.session}
                  onChange={(e) => setFormData({ ...formData, session: e.target.value })}
                  className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                  style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                  placeholder="e.g., Thai Herbs & Spices Cooking Class"
                />
              </div>

              <div>
                <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Class Date</label>
                <input
                  type="date"
                  value={formData.class_date}
                  onChange={(e) => setFormData({ ...formData, class_date: e.target.value })}
                  className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                  style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                />
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Start Time</label>
                  <input
                    type="time"
                    value={formData.start_time}
                    onChange={(e) => setFormData({ ...formData, start_time: e.target.value })}
                    className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                    style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                  />
                </div>
                <div>
                  <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>End Time</label>
                  <input
                    type="time"
                    value={formData.end_time}
                    onChange={(e) => setFormData({ ...formData, end_time: e.target.value })}
                    className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                    style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Menu ID</label>
                <input
                  type="number"
                  value={formData.menu_id}
                  onChange={(e) => setFormData({ ...formData, menu_id: parseInt(e.target.value) })}
                  className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                  style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                  placeholder="101"
                />
              </div>

              <div className="grid grid-cols-3 gap-4">
                <div>
                  <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Capacity</label>
                  <input
                    type="number"
                    value={formData.capacity}
                    onChange={(e) => setFormData({ ...formData, capacity: parseInt(e.target.value) })}
                    className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                    style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                    placeholder="12"
                  />
                </div>

                <div>
                  <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Enrolled</label>
                  <input
                    type="number"
                    value={formData.enrolled || 0}
                    onChange={(e) => setFormData({ ...formData, enrolled: parseInt(e.target.value) })}
                    className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                    style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                    placeholder="0"
                  />
                </div>

                <div>
                  <label className="block text-sm font-light mb-2" style={{ color: '#8b6f47' }}>Price (‡∏ø)</label>
                  <input
                    type="number"
                    value={formData.price}
                    onChange={(e) => setFormData({ ...formData, price: parseInt(e.target.value) })}
                    className="w-full border rounded-lg px-4 py-2 text-sm focus:outline-none transition"
                    style={{ borderColor: '#e5dcd4', backgroundColor: '#fdfaf6' }}
                    placeholder="1500"
                  />
                </div>
              </div>

              <div className="flex items-center gap-3 p-3 rounded-lg" style={{ backgroundColor: '#f5f1ed' }}>
                <input
                  type="checkbox"
                  checked={formData.is_special}
                  onChange={(e) => setFormData({ ...formData, is_special: e.target.checked })}
                  className="w-4 h-4"
                  id="is_special"
                />
                <label htmlFor="is_special" className="text-sm font-light" style={{ color: '#8b6f47' }}>
                  Mark as Special Class
                </label>
              </div>
            </div>

            <div className="flex gap-3 mt-6">
              <button
                onClick={() => setShowForm(false)}
                className="flex-1 px-4 py-2 rounded-lg font-light transition hover:opacity-80"
                style={{ color: '#3d2817', backgroundColor: '#e5dcd4' }}
              >
                Cancel
              </button>
              <button
                onClick={handleSaveCourse}
                className="flex-1 px-4 py-2 rounded-lg text-white font-light transition hover:shadow-lg"
                style={{ backgroundColor: '#3d2817' }}
              >
                {editingId ? 'Update' : 'Save'} Course
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}